@import url("https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;600;700&display=swap");

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
P {
  text-align: center;
}
body {
  margin: 0;
  padding: 0;
  font-family: "Quicksand", sans-serif;
}

.main-header {
  display: flex;
  text-decoration: none;
  justify-content: space-between;
  padding: 5px 10px 0px 10px;
  height: 44px;
  background-color: #1d1d1f;
  align-items: center;
  position: fixed;
  width: 100%;
  z-index: 2;
}

.main-header a {
  text-decoration: none;
  color: #f5f5f7;
}
.main-menu {
}
.main-menu a {
  text-decoration: none;
  padding-left: 10px;
  font-size: 12px;
  height: 100%;
}
.link-text {
  font-weight: 500;
}
.logo {
  padding-left: 10px;
}
.cart {
  padding-right: 10px;
}
.desktop {
  display: none;
}

@media (min-width: 900px) {
  .desktop {
    display: initial;
  }
}

.content {
  background-color: black;
  text-align: center;
  padding-top: 60px;
}

.first {
  height: 80vh;
  width: 100%;
  background-size: cover;

  background: url("https://www.apple.com/v/home/ay/images/heroes/iphone-14-pro/hero_iphone14pro__e5xbgo5ffhg2_small.jpg")
    no-repeat center center;
}
.second {
  height: 80vh;
  background-size: cover;
  background: url("https://www.apple.com/v/home/ay/images/heroes/iphone-14/hero_iphone14__fjmsqstr1ceq_small.jpg")
    no-repeat center center;
}

.third {
  height: 80vh;
  background-size: cover;
  background: url("https://www.apple.com/v/home/ay/images/heroes/apple-watch-series-8/hero_apple_watch_series_8__bjc1ok5mkjua_small.jpg")
    no-repeat center center;
}
h1 {
}
h3 {
  margin-top: 5px;
}

.link-container {
  padding-top: 14px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.link-container a {
  text-decoration: none;
  color: #2997ff;
  padding-right: 32px;
}
.content-conatiner {
  display: grid;
  grid-template-columns: 1fr;
  box-sizing: border-box;
  margin: 0px 4px 0px 4px;
}

.white {
  color: #f4f5f6;
}
.black {
  color: #1d1d1f;
}
.row-conatiner {
  display: flex;
  width: 100%;
  flex-direction: column;
  height: 55vh;
  align-items: center;
  padding-top: 40px;
}
.first-container {
  width: 100%;
  background-size: cover;

  background: url("https://www.apple.com/v/home/ay/images/promos/macbook-pro-14-and-16/promo_mbp__ek7p477bkp6q_small.jpg")
    no-repeat center center;
  margin-top: 4px;
  margin-bottom: 2px;
}
.second-container {
  width: 100%;
  background-size: cover;
  background: url("https://www.apple.com/v/home/ay/images/promos/homepod/promo_homepod_announce__bhviq9vjnlf6_small.jpg")
    no-repeat center center;
  margin-top: 2px;
  margin-bottom: 2px;
}
.third-container {
  width: 100%;
  background-size: cover;
  background: url("https://www.apple.com/v/home/ay/images/promos/watch-black-unity/promo_bhm__d8b9z5rlwjgy_small.jpg")
    no-repeat center center;
  margin-top: 2px;
  margin-bottom: 2px;
}
.fourth-container {
  width: 100%;
  background-size: cover;
  background: url("https://www.apple.com/v/home/ay/images/promos/valentines-day-2023/promo_valentines_day_2023__duffhlmdxcqe_small.jpg")
    no-repeat center center;
  margin-top: 2px;
  margin-bottom: 2px;
}
.fifth-container {
  width: 100%;
  background-size: cover;
  background: url("https://www.apple.com/v/home/ay/images/promos/apple-fitness-plus-iphone/promo-apple-fitness-plus-iphone__bzeal882mv7m_small.jpg")
    no-repeat center center;
  margin-top: 2px;
  margin-bottom: 2px;
}
.sixth-container {
  width: 100%;
  background-size: cover;
  background: url("https://www.apple.com/v/home/ay/images/promos/apple-card/tile__cauwwcyyn9hy_small.jpg")
    no-repeat center center;
  margin-top: 2px;
  margin-bottom: 4px;
}
h2 {
  text-align: center;
}
